import React from 'react';
import { Link, graphql } from 'gatsby';
import Loadable from 'react-loadable';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { Pane, Heading, Small, Badge, Text } from 'evergreen-ui';
import { useMediaQuery } from 'react-responsive';
import Subtitle from '../components/subtitle';

// Workaround as browser APIs are not available during build. Refer: https://github.com/gatsbyjs/gatsby/issues/15161#issuecomment-508817092
const Xarrow = Loadable({
  loader: () => import('react-xarrows'),
  loading() {
    return <div>Loading...</div>;
  },
});

const introductionId = 'intro-blog';

const EyeCue = ({ totalPosts }) => {
  const cues = [];
  for (let i = 0; i < totalPosts - 1; i++) {
    cues.push(
      <Xarrow
        key={i}
        start={`post-${i}`}
        end={`post-${i + 1}`}
        dashness={{ animation: 3 }}
        showHead={false}
        strokeWidth={2}
        curveness={0}
        lineColor="#E4E7EB"
      />
    );
  }
  cues.push(
    <Xarrow
      key={totalPosts}
      dashness={{ animation: 3 }}
      showHead={false}
      strokeWidth={2}
      curveness={0}
      lineColor="#E4E7EB"
      start={introductionId}
      end="post-0"
      startAnchor="bottom"
    />
  );
  return cues;
};

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const posts = data.allMarkdownRemark.edges.filter(
    (edge) => edge.node.frontmatter.template === 'post'
  );
  const isSingleColumn = useMediaQuery({ query: '(max-width: 915px)' });

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="All posts" />
      <Subtitle
        id={introductionId}
        title="BLOG"
        sub="Tap a bit into my mind from my writings 💭"
      />
      <Pane display="flex" flexWrap="wrap" justifyContent="space-around">
        {posts.map(({ node }, i) => {
          const title = node.frontmatter.title || node.fields.slug;
          return (
            <Pane
              key={i}
              marginTop={i % 2 === 1 ? '40px' : '0px'}
              width="450px"
              paddingLeft="50px"
              paddingRight="50px"
              paddingBottom="30px"
            >
              <Link to={`blog${node.fields.slug}`}>
                <Heading textAlign="center" size={600} id={`post-${i}`}>
                  {title}
                </Heading>
              </Link>

              <Pane display="flex" justifyContent="center">
                {node.frontmatter.tags.map((tag, i) => (
                  <Badge key={i} marginRight="10px" color="teal">
                    {tag}
                  </Badge>
                ))}
              </Pane>

              <Pane display="flex" justifyContent="center">
                <Text color="muted">{node.frontmatter.date}</Text>
              </Pane>

              <Text size={500} textAlign="justify">
                <p>{node.frontmatter.description || node.excerpt}</p>
              </Text>
            </Pane>
          );
        })}
      </Pane>
      {!isSingleColumn && <EyeCue totalPosts={posts.length} />}
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            template
            title
            tags
            description
            slug
            category
          }
        }
      }
    }
  }
`;
